@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Playfair+Display:ital@1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap');
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Serif&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Alegreya&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond&display=swap');

@import url('./small-screens.css');
@import url('./styles/landing-page.css');
@import url('./styles/shop-page.css');
@import url('./styles/item-showcase.css');
@import url('./styles/checkout.css');

:root {
    --h-text: 29;
    --s-text: 16%;
    --l-text: 26%;
    --h-background: 0;
    --s-background: 0%;
    --l-background: 74%;
    --background-color: hsla(0, 0%, 83%, 0.37);
    --text-color: #4d4238;
    --container-color: rgba(165, 165, 165, 0.281);
    --button-border-color: var(--text-color);
}

html {
    width: 100%;
}

body,
#root {
    padding: 0;
    margin: 0;
    font-size: 1.2rem;
}

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-thumb {
    background-color: black;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    justify-content: space-between;
    font-family: 'Cormorant Garamond', serif;
    background-color: var(--background-color);
    color: var(--text-color);
}

footer {
    flex: 0 1 auto;
    padding: 1rem;
    text-align: center;
}

#header {
    display: grid;
    grid-template-columns: 25% 50% 25%;
    font-size: 1.5rem;
    width: 100vw;
    align-items: center;
    font-weight: 600;
    border-bottom: 1px solid var(--secondary-second);
    background-color: var(--background-color)
}

#header:hover a {
    opacity: 1;
    transition: opacity 500ms;
    z-index: 1;
}

.logo-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: fit-content;
    margin: auto;
    text-align: center;
}

img[alt="logo-watch"] {
    height: 50px;
    color: var(--text-color);
}

#links {
    /* flex: 0 1 auto; */
    display: flex;
    flex-direction: row;
    gap: 2rem;
    padding-left: 1rem;
    margin: auto;

}

#links a {
    text-decoration: none;
    position: relative;
    opacity: 1;
    color: var(--text-color);
}

#links a:hover {
    text-decoration: underline;
}

#cart-shortcut {
    /* flex: 0 1 auto; */
    display: flex;
    flex-direction: row;
    gap: 0.4rem;
    padding-right: 1rem;
    margin: auto;

}

.cart-shortcut-container {
    margin: auto;
}

img[alt='cart-svg'] {
    color: var(--text-color);
    width: 35px;
}

.cart-add {
    animation: cart-animation-add;
    animation-duration: 350ms;
}

@keyframes cart-animation-add {
    0% {
        filter: invert(100%) sepia(2%) saturate(7487%) hue-rotate(196deg) brightness(105%) contrast(100%);
    }

    50% {
        filter: invert(73%) sepia(10%) saturate(5885%) hue-rotate(40deg) brightness(102%) contrast(85%);
    }

    100% {
        filter: invert(100%) sepia(2%) saturate(7487%) hue-rotate(196deg) brightness(105%) contrast(100%);
        transform: rotate(360deg);
    }
}

.cart-remove {
    animation: cart-animation-remove;
    animation-duration: 350ms;
}

@keyframes cart-animation-remove {
    0% {
        filter: invert(100%) sepia(2%) saturate(7487%) hue-rotate(196deg) brightness(105%) contrast(100%);
    }

    50% {
        filter: invert(9%) sepia(88%) saturate(6796%) hue-rotate(359deg) brightness(99%) contrast(110%);
        transform: translateX(10px);
    }

    100% {
        filter: invert(100%) sepia(2%) saturate(7487%) hue-rotate(196deg) brightness(105%) contrast(100%);
        transform: translateX(0);
    }
}

#items-count {
    font-size: 1.4rem;
    padding: 0.4rem;
    color: var(--text-color);
}

button {
    font-size: 1rem;
    font-family: 'Cormorant Garamond', serif;
    /* background-color: var(--background-color); */
    background-color: inherit;
    border: 1px solid var(--button-border-color);
    color: var(--text-color);
    padding: 0.6rem;
    font-weight: 600;
}

img.filter-img {
    filter: invert(22%) sepia(5%) saturate(2183%) hue-rotate(347deg) brightness(100%) contrast(84%);
}

/* MEDIA SCREEN */
/*              */
/*              */
@media screen and (max-width : 1100px) {
    #citation {
        position: relative;
        top: unset;
        color: var(--text-color);
    }

    p.intro {
        font-size: 1.2rem;
        animation: fadeIn 8000ms forwards;
        text-align: center;
    }

    p.intro:nth-of-type(1) {
        font-family: 'Playfair Display', serif;
        font-size: 1.1rem;
    }

    .vitrine-title,
    .lorem {
        animation: none;
    }

}

@media screen and (max-width : 800px) {
    .logo-container {
        font-size: 1rem;
    }
}

@media screen and (max-width : 630px) {
    #links {
        font-size: 1.3rem;
        gap: 0.2rem;
        flex-direction: column;
        padding: 0.4rem;
    }

    .logo-container h1 {
        font-size: 1.4rem;
    }

    img[alt='cart-svg'] {
        /* filter: invert(100%) sepia(2%) saturate(7487%) hue-rotate(196deg) brightness(105%) contrast(100%); */
        width: 25px;
    }
}

.about {
    padding: 3rem;
}